.hero-slider__icon {
  position: absolute;
  height: 40px;
  width: 40px;
  z-index: 1;
  top: 50%;
  transform: translateY(-50%);

  &--prev {
    left: 15px;
  }

  &--next {
    right: 15px;
  }

  border: solid 1.25px;
  border-color: transparent;
  fill: var(--accent-color);
  background-color: rgba(0, 0, 0, 0.65);

  @include tablet {
    &--prev {
      left: 47px;
    }

    &--next {
      right: 47px;
    }
  }

  @include desktop {
    &--prev {
      left: 85px;
    }

    &--next {
      right: 85px;
    }
  }
}