.animation-border {
  position: relative;
}

.animation-border:before {
  content: "";
  width: calc(100% + 4px);
  height: calc(100% - 12px);
  opacity: 0;
  background-color: #fff;
  transition-duration: .35s;
  transition-timing-function: cubic-bezier(.4, 0, .2, 1);
  position: absolute;
  top: 6px;
  left: -2px;
  transform: scaleY(0);
}

.animation-border:hover:before {
  opacity: 1;
  transform: scaleY(1);
}

.animation-border:after {
  content: "";
  height: calc(100% + 4px);
  width: calc(100% - 12px);
  opacity: 0;
  background-color: #fff;
  transition-duration: .35s;
  transition-timing-function: cubic-bezier(.4, 0, .2, 1);
  position: absolute;
  top: -2px;
  left: 6px;
  transform: scaleX(0);
}

.animation-border:hover:after {
  opacity: 1;
  transform: scaleX(1);
}

.animation-border-wrap {
  z-index: 3;
  align-items: center;
  display: flex;
  position: relative;
}

